@use "../variables" as v;
@use "../mixin" as m;

// All pages

#menu-all-pages{


}

.header-area .container .row{
    align-items: center;
}

.main-menu{
    ul{
        margin: 0;
        padding: 0;
        position: relative;
        display: block !important;

        li{
            display: inline-block;
            padding: 0;
            position: relative;
             transition: all 0.4s;

            a{
                padding: 50px 45px 50px 0;
                display: block;
                font-size: 16px;

            }

            &:hover{
                a{
                    color: v.$primary-color;
                }
            }

            
            ul{
                background-color: #ffffff;
                left: 0;
                opacity: 0;
                position: absolute;
                text-align: left;
                top: 100%;
                visibility: hidden;
                z-index: 99999;
                transition: all 300ms ease;
                border-top: 2px solid v.$primary-color;
                box-shadow: 0px 10px 20px 0px rgba(8, 0, 42, 0.08);
                transform-origin: top;
                transform: scaleY(0);
    
                li{
                    float: none;
                    min-width: 210px;
    
                    ul{
                       
                        visibility: hidden;
                        left: 100%;
                        
                    }
    
                    a{
                        padding: 10px 20px;
                        color: #23262D;
                        font-size: 16px;
                    }
               
                }
    
           
            }
        }
    }
}
.main-menu ul li:hover > ul {
    visibility: visible ;
    transition: all 300ms ease;
    opacity: 1;
    transform: scaleY(1);
}

.main-menu ul ul li {
    float: none;
    min-width: 210px;
}
.main-menu ul li ul li:hover > ul {
    visibility: visible;
    top: -2px;
}

.number-with-btn{
    .phone-number{
        display: none;
    }
}